'use client';

import React, { useEffect, useState, useRef } from 'react';
import PropertiesComponent from './components/PropertiesComponent';
import LeadsComponent from './components/LeadsComponent';
import BrokersComponent from './components/BrokersComponent';
import HeaderFile from '../components/Header';

const Search = () => {
  const [activeTab, setActiveTab] = useState('brokers');
  const [searchQuery, setSearchQuery] = useState('');
  const lastManualTabChangeRef = useRef(null);

  // Read query param without useSearchParams to avoid Suspense requirement
  useEffect(() => {
    if (typeof window === 'undefined') return;
    const updateFromURL = () => {
      try {
        const sp = new URLSearchParams(window.location.search);
        const t = sp.get('tab');
        if (t === 'leads' || t === 'brokers' || t === 'properties') {
          setActiveTab(t);
        } else if (!t) {
          // If no tab in URL, default to brokers
          setActiveTab('brokers');
        }
        // Set search query from URL if present (works with both regionId and q)
        const q = sp.get('q');
        if (q) {
          setSearchQuery(q);
        } else {
          setSearchQuery('');
        }
      } catch {}
    };
    // Only read from URL on initial mount
    updateFromURL();
  }, []);

  // Listen for URL changes (back/forward buttons and navigation)
  useEffect(() => {
    if (typeof window === 'undefined') return;
    
    const handlePopState = () => {
      try {
        const sp = new URLSearchParams(window.location.search);
        const t = sp.get('tab');
        // Only update if it's been more than 1 second since last manual change
        // This prevents the interval from overriding manual tab clicks
        const now = Date.now();
        if (lastManualTabChangeRef.current && (now - lastManualTabChangeRef.current) < 1000) {
          return; // Ignore URL changes if manual change was recent
        }
        
        if (t === 'leads' || t === 'brokers' || t === 'properties') {
          setActiveTab(t);
        } else if (!t) {
          setActiveTab('brokers');
        }
        // Set search query from URL if present (works with both regionId and q)
        const q = sp.get('q');
        if (q) {
          setSearchQuery(q);
        } else {
          setSearchQuery('');
        }
      } catch {}
    };
    
    // Listen for manual tab changes from TabsBar component
    const handleTabChanged = (event) => {
      lastManualTabChangeRef.current = Date.now();
      // State is already updated by TabsBar, just mark the timestamp
    };
    
    // Listen for popstate (browser back/forward)
    window.addEventListener('popstate', handlePopState);
    window.addEventListener('tabChanged', handleTabChanged);
    
    // Check URL changes periodically but less frequently (every 1000ms)
    // Only to catch external navigation changes (like from navbar)
    let lastUrl = window.location.href;
    const checkUrlChange = () => {
      const currentUrl = window.location.href;
      if (currentUrl !== lastUrl) {
        lastUrl = currentUrl;
        handlePopState();
      }
    };
    
    const urlCheckInterval = setInterval(checkUrlChange, 1000);
    
    return () => {
      window.removeEventListener('popstate', handlePopState);
      window.removeEventListener('tabChanged', handleTabChanged);
      clearInterval(urlCheckInterval);
    };
  }, []);

  // Update URL when tab changes
  const handleTabChange = (tab) => {
    setActiveTab(tab);
    if (typeof window !== 'undefined') {
      const url = new URL(window.location);
      url.searchParams.set('tab', tab);
      window.history.pushState({}, '', url);
    }
  };

  const getPageTitle = () => {
    switch (activeTab) {
      case 'properties':
        return 'Properties';
      case 'leads':
        return 'Leads';
      case 'brokers':
        return 'Brokers';
      default:
        return 'Search';
    }
  };

  const getHeaderData = () => {
    switch (activeTab) {
      case 'properties':
        return {
          title: 'Find Your Dream Property',
          description: 'Discover properties that match your preferences, budget, and location requirements.',
        };
      case 'leads':
        return {
          title: 'Discover Quality Enquiries',
          description: 'Browse and connect with potential clients looking for real estate services in your area.',
        };
      case 'brokers':
        return {
          title: 'Find the Perfect Broker for Your Needs',
          description: 'Browse verified brokers matched to your location, category, and budget.',
        };
      default:
        return {
          title: 'Find the Perfect Broker for Your Needs',
          description: 'Browse verified brokers matched to your location, category, and budget.',
        };
    }
  };

  const renderContent = () => {
    switch (activeTab) {
      case 'properties':
        return <PropertiesComponent activeTab={activeTab} setActiveTab={setActiveTab} />;
      case 'leads':
        return <LeadsComponent activeTab={activeTab} setActiveTab={setActiveTab} />;
      case 'brokers':
        return <BrokersComponent activeTab={activeTab} setActiveTab={setActiveTab} initialSearchQuery={searchQuery} />;
      default:
        return <PropertiesComponent activeTab={activeTab} setActiveTab={setActiveTab} />;
    }
  };

  return (
    <div className="min-h-screen bg-white ">
      <HeaderFile data={getHeaderData()} />

      <div className="w-full mx-auto px-4 md:px-0 py-8">
        {/* Render Active Component */}
        {renderContent()}
      </div>
    </div>
  );
};

export default Search;
